.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  box-sizing: border-box;
  padding: 0 1em;
  cursor: auto;
  user-select: none;
  vertical-align: top;
  text-decoration: none;
  transition: opacity 200ms;
  color: inherit;
  border: none;
  background-color: transparent;
  font-family: inherit;
  font-weight: 500;
}
button:not(:disabled).button,
a[href].button,
label.button,
span.button {
  cursor: pointer;
}
button:not(:disabled).button:active, button:not(:disabled).button_pressed,
a[href].button:active,
a[href].button_pressed,
label.button:active,
label.button_pressed,
span.button:active,
span.button_pressed {
  opacity: 0.5;
}

.button:disabled, .button_disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
.button:focus {
  outline: none;
}
.button:focus-visible, .button_focused {
  box-shadow: 0 0 0 2px #81aeff80;
}
.button img,
.button svg {
  display: inherit;
  vertical-align: inherit;
}
.button_transparent {
  min-height: 40px;
  transition: all 200ms ease;
  color: #00000080;
  border: 2px solid #00000080;
  border-radius: 30px;
}
.button_transparent:hover, .button_transparent:focus {
  opacity: 0.7;
}
.button_transparent:active {
  opacity: 0.5;
}

.button__children {
  overflow: hidden;
  pointer-events: none;
  white-space: nowrap;
  text-overflow: ellipsis;
}

