.input__control {
  position: relative;
  display: inline-flex;
  width: 100%;
  padding: 0;
  vertical-align: top;
  color: inherit;
  border: none;
  background-color: transparent;
  font: inherit;
}
.input__control:focus + .input__label, .input__control:not(:placeholder-shown) + .input__label {
  top: 0;
  transform: translateY(0);
}
.input__control:focus {
  outline: none;
}
.input__control:focus::placeholder {
  visibility: visible;
}
.input__control::placeholder {
  visibility: hidden;
  color: inherit;
  opacity: 0.2;
}

