.dropdown-menu__button {
  position: relative;
}
.dropdown-menu__button:hover .dropdown-menu__item-list, .dropdown-menu__button:focus-within .dropdown-menu__item-list {
  display: block;
}

.dropdown-menu__item-list {
  position: absolute;
  z-index: 1;
  top: 100%;
  right: -38px;
  display: none;
  width: 250px;
  margin: 0;
  padding: 15px 0 0;
  list-style: none;
  filter: drop-shadow(2px 4px 6px #00000033);
}

.dropdown-menu__item-list-item {
  box-sizing: border-box;
  height: 48px;
  width: 100%;
  transition: border-color 200ms, background-color 200ms;
  border: 1px solid transparent;
  background-color: #ffffff;
}
.dropdown-menu__item-list-item:hover, .dropdown-menu__item-list-item:focus-within {
  border-top-color: #bfdcf7;
  border-bottom-color: #bfdcf7;
  background-color: #f2f8fd;
}
.dropdown-menu__item-list-item:first-child {
  position: relative;
  border-top-color: transparent;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.dropdown-menu__item-list-item:first-child:hover::before, .dropdown-menu__item-list-item:first-child:focus-within::before {
  border-bottom-color: #f2f8fd;
}
.dropdown-menu__item-list-item:first-child::before {
  position: absolute;
  top: 0;
  right: 20%;
  margin-top: -27px;
  content: "";
  transition: border-bottom-color 200ms;
  border: 12px solid transparent;
  border-bottom: 15px solid #ffffff;
}
.dropdown-menu__item-list-item:last-child {
  border-bottom-color: transparent;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.dropdown-menu__link {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  cursor: auto;
  text-decoration: none;
  color: #00000080;
  font-weight: 700;
}
.dropdown-menu__link[href] {
  cursor: pointer;
  color: #000000;
}
.dropdown-menu__link:focus {
  outline: none;
}
.dropdown-menu__link_current {
  font-weight: 400;
}